var LOCALDATABASE_NAME = 'localstorage';

var is_developing = true;

var SERVER_URL;

if (is_developing) {
  SERVER_URL = 'http://localhost:3003';
} else {
  SERVER_URL = 'http://localhost:3003';
}

module.exports = {
  'SERVER_URL': SERVER_URL,
  'LOCALDATABASE_NAME': LOCALDATABASE_NAME
}



